.DatasetSelected {
  width: 820px;
  margin: 0 0 0 -20px;
  border: 1px solid #BBD7F2;
  border-radius: 4px;
  background: #FFF; }

.DatasetSelected-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px; }

.DatasetSelected-itemExt {
  width: 38px;
  min-width: 38px;
  height: 38px;
  border: 1px solid #199BDB;
  border-radius: 4px;
  color: #419CDB;
  font-size: 12px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase; }

.DatasetSelected-itemInfo {
  margin-left: 20px; }

.DatasetSelected-itemTitle {
  display: block;
  color: #333333;
  font-size: 16px;
  font-weight: 500; }

.DatasetSelected-itemDescription {
  display: block;
  margin-top: 3px;
  color: #999999;
  font-size: 13px; }

.DatasetSelected-sync {
  display: block;
  overflow: hidden;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  background: #FFF; }

.DatasetSelected-syncOptions {
  display: flex;
  align-items: center;
  margin: 0 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 1px solid #dddddd; }

.DatasetSelected-syncLabel {
  width: 100px;
  border-right: 1px solid rgba(0, 0, 0, 0.06); }

.DatasetSelected-syncOptionsList {
  display: flex;
  align-items: center; }

.DatasetSelected-syncOptionsList--syncView {
  margin-left: auto; }

.DatasetSelected-syncOptionsItem {
  margin-left: 24px; }

.UpgradeElement.DatasetSelected-upgrade {
  padding: 20px;
  border-top: 1px solid #BBD7F2 !important;
  border-right: 0;
  border-bottom: 0;
  border-left: 0;
  border-radius: 0 0 4px 4px; }

.DatasetSelected-upgradeButton {
  padding-right: 40px;
  padding-left: 40px; }
