/* FONT FACE */
.ServiceIcon {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 12px;
  background: url("/assets/1.0.0-assets.136/images/layout/new_import_icons.png") no-repeat 0 0;
  vertical-align: middle; }
  @media (-webkit-min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
    .ServiceIcon {
      /* on retina, use image that's scaled by 2 */
      background: url("/assets/1.0.0-assets.136/images/layout/new_import_icons@2x.png");
      background-position: 0 0;
      background-size: 223px 40px; } }

.ServiceIcon--dropbox {
  width: 22px;
  height: 20px;
  background-position: -18px -20px; }

.ServiceIcon--gdrive {
  width: 25px;
  height: 20px;
  background-position: -40px -20px; }

.ServiceIcon--mailchimp {
  width: 18px;
  height: 20px;
  background-position: -65px -20px; }

.ServiceIcon--instagram {
  width: 20px;
  height: 20px;
  background-position: -145px -20px; }

.ServiceIcon--box {
  width: 29px;
  height: 20px;
  background-position: -194px -20px; }
