/* FONT FACE */
.OrganizationNotifications {
  margin: 40px 0 100px; }

.OrganizationNotifications-button {
  height: 34px; }

.OrganizationNotifications-textarea {
  height: 80px; }

.Md-container {
  position: absolute;
  z-index: 10;
  right: 1px;
  bottom: 1px;
  padding: 8px;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.6); }

.field_with_errors .OrganizationNotifications-textarea {
  border: 1px solid rgba(241, 87, 67, 0.48);
  background: rgba(241, 87, 67, 0.04);
  color: #f15743; }

.field_with_errors + .Md-container {
  bottom: 3px; }

.Md-icon,
.Md-text {
  float: left; }

.Md-icon {
  width: 24px;
  height: 16px;
  border-radius: 2px;
  background: #DDD;
  text-align: center; }

.Md-buttonInner {
  position: relative;
  top: -2px; }

.NotificationsList {
  display: flex;
  position: relative;
  flex-direction: column;
  width: 100%;
  margin: 0 auto; }

.NotificationsList-item {
  display: flex;
  position: relative;
  box-sizing: border-box;
  margin-bottom: -1px;
  padding: 20px 0;
  border: 1px solid transparent;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06); }
  .NotificationsList-item:first-child {
    padding-top: 0; }

.NotificationsList-itemCategory {
  display: inline-block;
  position: relative;
  z-index: 1;
  min-width: 40px;
  height: 40px;
  margin-top: 3px;
  margin-right: 16px;
  transition: opacity 250ms ease-in-out;
  border: none;
  opacity: 1;
  background: url("/assets/1.0.0-assets.136/images/layout/notification-types-icons.png") no-repeat 0 0; }
  @media (-webkit-min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
    .NotificationsList-itemCategory {
      /* on retina, use image that's scaled by 2 */
      background: url("/assets/1.0.0-assets.136/images/layout/notification-types-icons@2x.png");
      background-position: 0 0;
      background-size: 40px 40px; } }

.NotificationsList-itemCategory--warning {
  background: url("/assets/1.0.0-assets.136/images/layout/notification-types-icons.png") no-repeat 0 0; }
  @media (-webkit-min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
    .NotificationsList-itemCategory--warning {
      /* on retina, use image that's scaled by 2 */
      background: url("/assets/1.0.0-assets.136/images/layout/notification-types-icons@2x.png");
      background-position: 0 0;
      background-size: 40px 40px; } }

.NotificationsList-itemInfo {
  display: flex;
  position: relative;
  z-index: 1;
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  transition: opacity 250ms;
  opacity: 1; }

.NotificationsList-itemPrimaryInfo {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 400px;
  margin-right: 20px; }

.NotificationsList-itemSecondaryInfo {
  display: flex;
  flex-direction: column;
  margin-top: 0; }

.NotificationsList-itemMeta {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 8px; }

.NotificationsList-itemTitle {
  display: flex;
  align-items: center;
  margin-bottom: 0;
  word-wrap: break-word;
  word-break: break-word;
  overflow-wrap: break-word; }
  .NotificationsList-itemTitle ul,
  .NotificationsList-itemTitle ol {
    padding-left: 20px;
    list-style-type: disc; }
  .NotificationsList-itemTitle ol {
    list-style-type: decimal; }
  .NotificationsList-itemTitle ul {
    list-style-type: disc; }
  .NotificationsList-itemTitle em {
    font-style: italic; }
  .NotificationsList-itemTitle strong {
    font-weight: 600; }
  .NotificationsList-itemTitle code {
    margin: 0;
    padding: 3px;
    border-radius: 3px;
    background-color: rgba(0, 0, 0, 0.05);
    font-size: 85%;
    line-height: 25px; }

.NotificationsList-itemDescription {
  height: 16px;
  margin-top: 4px; }
  .NotificationsList-itemDescription abbr[title] {
    border: 0; }

.NotificationsList-itemLink {
  font-size: 12px; }

.Md-counter.Md-counter--negative,
.NotificationsList-itemLink--remove {
  color: #f15743; }
