.ErrorDetails-header .Dialog-headerIcon {
  margin-bottom: 15px;
  line-height: 40px; }

.ErrorDetails-header .Dialog-headerTitle {
  color: #f15743; }

.ErrorDetails-header .Dialog-headerText {
  font-size: 14px; }

.ErrorDetails-body {
  width: 620px;
  min-width: initial; }

.ErrorDetails-body--no-line {
  padding: 0;
  border-top: none; }

.ErrorDetails-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px 0;
  border-bottom: 1px solid #dddddd; }

.ErrorDetails-item:first-child {
  padding-top: 0; }

.ErrorDetails-item:last-child {
  padding-bottom: 0;
  border-bottom: none; }

.ErrorDetails-itemStep {
  width: 38px;
  height: 38px;
  margin-right: 20px;
  border: 1px solid #CCC;
  border-radius: 38px;
  color: #666666;
  font-size: 13px;
  line-height: 38px;
  text-align: center; }

.ErrorDetails-itemIcon {
  width: 40px;
  height: 40px;
  margin: 0 auto;
  border: 1px solid rgba(0, 0, 0, 0.06);
  border-radius: 3px;
  font-size: 22px;
  line-height: 44px; }

.ErrorDetails-itemIcon--success {
  border-color: #9BC63B;
  color: #9BC63B; }

.ErrorDetails-itemIcon--alert {
  border-color: orange;
  color: orange; }

.ErrorDetails-itemStep--lessProminent {
  border-color: #DDD;
  color: #DDD; }

.ErrorDetails-itemText {
  width: 500px; }

.ErrorDetails-itemTextStrong {
  color: #333333; }

.ErrorDetails-footer {
  width: 620px;
  margin: 0 auto;
  text-align: center; }

.ErrorDetails-footer--no-line {
  border-top: none; }

.ErrorDetails-footerButton {
  display: inline-block;
  padding-right: 40px;
  padding-left: 40px;
  line-height: 40px; }
