.ImportPanel {
  width: 780px;
  margin: 0 auto; }

.ImportPanel-header {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 28px 0;
  text-align: center; }

.ImportPanel-headerIcon {
  display: inline-block;
  margin-bottom: 20px;
  text-align: center; }

.ImportPanel-headerDescription {
  width: 465px;
  margin: 5px auto 0; }

.ImportPanel-headerDescription--normalWeight {
  font-weight: 500; }

.ImportPanel-headerDescription--withoutWidth {
  width: auto; }

.ImportPanel-headerDescription--negative {
  color: #f15743; }

.ImportPanel-headerDescription--negative,
.ImportPanel-headerDescriptionLink {
  color: #f15743;
  text-decoration: underline; }
  .ImportPanel-headerDescription--negative:hover,
  .ImportPanel-headerDescriptionLink:hover {
    color: #f15743; }

.ImportPanel-headerButton {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -20px; }

.ImportPanel-headerLink {
  margin-top: 30px; }

.ImportPanel-body {
  display: block;
  position: relative;
  width: 100%; }

.ImportPanel-bodyWrapper {
  margin: 0 auto; }

.ImportPanel-state {
  display: none;
  width: 780px; }

.ImportPanel-list {
  width: 320px;
  margin: 0 auto;
  padding-left: 80px;
  list-style-type: disc; }

.ImportPanel-listItem {
  margin-bottom: 10px;
  color: #666666;
  font-size: 14px;
  font-weight: 300; }

.ImportPanel-actions {
  text-align: center; }

.ImportPanel-actionsButton {
  display: inline-block; }
