.Toggler {
  display: inline-block;
  position: relative;
  width: 36px;
  height: 20px; }

.Toggler.is-disabled {
  opacity: 0.4; }
  .Toggler.is-disabled label {
    cursor: default; }

.Toggler input {
  visibility: hidden; }

.Toggler label {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 36px;
  height: 20px;
  transition: all 150ms ease;
  border-radius: 30px;
  background-color: #DDD;
  cursor: pointer; }

.Toggler label::before {
  content: ' ';
  display: block;
  position: absolute;
  top: 4px;
  left: 4px;
  width: 12px;
  height: 12px;
  transition: all 150ms ease;
  border-radius: 30px;
  background-color: #FFF; }

.Toggler input:checked + label::before {
  left: 20px; }

.Toggler input:checked + label {
  background-color: #73C86B; }

.Toggler--grey label {
  width: 34px;
  height: 18px;
  border: 1px solid #BBB;
  background: #FFF;
  box-shadow: none; }

.Toggler--grey label::before {
  top: 3px;
  background: #BBB; }

.Toggler--grey input:checked + label::before {
  left: 19px; }

.Toggler--grey input:checked + label {
  background-color: #FFF; }
