.Onboarding {
  position: fixed;
  z-index: 10000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; }

.AnalysisCompletionDetails {
  display: flex;
  height: 100%; }

.Onboarding-fake {
  flex: 0 0 415px;
  height: 100%; }
  .is-editing .Onboarding-fake {
    flex: 0 0 560px; }

.Onboarding-body {
  width: 466px; }

.Onboarding-contentWrapper {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
  opacity: 0.88;
  background: #2E3C43;
  pointer-events: all; }

.Onboarding-headerTitle {
  margin-bottom: 12px;
  color: #FFF;
  font-size: 26px;
  font-weight: 300; }

.Onboarding-headerText {
  width: 550px;
  margin-bottom: 28px;
  color: #FFF;
  font-size: 26px;
  font-weight: 500;
  line-height: 22px; }

.Onboarding-headerDescription {
  margin-bottom: 28px;
  color: #FFF;
  font-size: 15px;
  font-weight: 500;
  line-height: 22px; }

.Onboarding-description {
  margin-bottom: 12px;
  color: #FFF;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px; }

.Onboarding-footer {
  margin-top: 40px; }

.Onboarding-footerButtons {
  margin-bottom: 20px; }

.Onboarding-footer--marginRight {
  margin-right: 16px; }

.Onboarding-list {
  margin-left: 28px; }

.Onboarding-listItem {
  margin-bottom: 12px;
  color: #FFF;
  list-style: disc; }

.Onboarding-listItemValue {
  display: inline-block;
  margin-bottom: 8px;
  padding: 4px 5px;
  border-radius: 2px;
  background: #1E272B;
  color: #FFF;
  font-family: 'Monaco', 'Monospace';
  font-size: 12px;
  font-weight: 500; }

.Onboarding-listItem .Onboarding-listItemDescription {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px; }

.Onboarding-readMore {
  color: #FFF;
  text-decoration: underline; }

.Onboarding-readMore:hover {
  text-decoration: none; }

.Onboarding.is-closing {
  /* "forwards" to keep the last keyframe's styles after animation ended */
  animation: fade-and-scale-out 80ms ease-in forwards; }

.Onboarding.is-opening {
  animation: fade-and-scale-in 100ms ease-out; }

@keyframes fade-and-scale-out {
  0% {
    transform: scale(1);
    opacity: 1; }
  80% {
    transform: scale(1.4);
    opacity: 0.05; }
  100% {
    transform: scale(2);
    opacity: 0; } }

@keyframes fade-and-scale-in {
  0% {
    transform: scale(2);
    opacity: 0; }
  10% {
    transform: scale(1.1);
    opacity: 0.9; }
  100% {
    transform: scale(1);
    opacity: 1; } }

.has-dark-text .Onboarding-headerTitle,
.has-dark-text .Onboarding-headerText,
.has-dark-text .Onboarding-headerDescription,
.has-dark-text .Onboarding-description,
.has-dark-text .Onboarding-listItem,
.has-dark-text .Onboarding-listItemValue,
.has-dark-text .Onboarding-readMore {
  color: #333; }
