/* FONT FACE */
.Footer {
  display: block;
  position: relative;
  width: 100%;
  padding: 40px 0;
  border-top: 1px solid rgba(46, 60, 67, 0.08);
  background: #F9F9F9; }

.Footer--light {
  background: WHITE; }

.Footer.Footer--public {
  padding-bottom: 0;
  overflow: hidden;
  border-top: 0;
  box-shadow: none;
  -webkit-font-smoothing: antialiased;
  -webkit-text-rendering: optimizeLegibility;
  text-rendering: optimizeLegibility; }

.Footer-inner {
  margin-top: 40px; }

.Footer-inner.Footer-inner--public * {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.Footer-inner .Footer-logo {
  position: absolute;
  top: 14px;
  left: 50%;
  margin-left: -20px; }

.Footer-inner--public .Footer-logo {
  position: static;
  top: auto;
  left: auto;
  margin: 0; }

.Footer-columns {
  border-top: 1px solid #EEE; }

.Footer-columns--footer {
  padding-bottom: 60px; }
  @media (max-width: 760px) {
    .Footer-columns--footer {
      padding-bottom: 40px; } }

.Footer-columns--noBorder {
  border-top: 0; }

.Footer-column {
  padding-bottom: 40px; }

.Footer-column--mobile {
  border-top: 1px solid #EEE; }

.Footer-columnTitle {
  margin-bottom: 12px;
  color: #2E3C43;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase; }

.Footer-link {
  transition: color 0.2s ease;
  color: #3AA9E3;
  line-height: 28px; }
  .Footer-link:hover {
    color: #2E3C43;
    text-decoration: none; }

.Footer-logo,
.Footer-credits {
  display: inline-block;
  vertical-align: middle; }

.Footer-credits {
  color: #979EA1;
  text-align: left; }

.Footer-title {
  display: block;
  color: #2E3C43;
  font-weight: 600; }

.Colophon {
  padding-top: 13px;
  text-align: right; }
  @media (max-width: 750px) {
    .Colophon {
      padding-top: 0;
      text-align: left; } }

.Colophon-item {
  display: inline-block; }

.Footer-item {
  display: block; }

.Newsletter {
  position: relative; }
  .Newsletter .hs-form {
    margin-right: 0 !important;
    padding-left: 0; }
    .Newsletter .hs-form .input {
      margin-right: 0;
      margin-bottom: 0; }
    .Newsletter .hs-form .hs-input {
      padding-right: 88px; }

.hs-newsletter {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100px;
  transition: all 0.15s;
  border: 0;
  border-radius: 0 4px 4px 0;
  outline: none;
  background: #3AA9E3;
  color: #FFF;
  font: 400 13px Lato, sans-serif;
  text-transform: none;
  cursor: pointer; }
  .hs-newsletter:hover {
    background: #249fe0;
    color: #FFF; }

@media only screen and (max-width: 400px) {
  .Footer-inner .Footer-logo {
    position: absolute;
    left: 50%;
    margin-left: -20px; }
  .Footer-inner--public .Footer-logo {
    position: static;
    top: auto;
    left: auto;
    margin: 0; }
  .Footer-logo.Logo.Logo--grey {
    top: 25px; }
  .Footer .Footer-inner {
    margin-top: 45px; } }

@media (min-width: 760px) {
  .Footer-list {
    display: flex;
    align-items: center;
    width: 450px;
    padding: 0 14px; }
  .Footer-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto; }
  .Footer-inner .Footer-logo {
    position: static;
    top: auto;
    left: auto;
    margin: 0; }
  .Footer-list--primary {
    justify-content: flex-start; }
  .Footer-list--secondary {
    justify-content: flex-end; }
  .Footer-listItem {
    padding: 0 12px; }
  .Footer-listItem:first-child {
    padding-left: 0; }
  .Footer-listItem:last-child {
    padding-right: 0; } }

@media (min-width: 960px) {
  .Footer-list {
    padding: 0; }
  .Footer-inner .Footer-logo {
    display: inline-block;
    position: relative;
    top: auto;
    left: auto;
    margin-left: auto; } }
