/* FONT FACE */
.NavButton {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 1px solid #bbbbbb;
  border-radius: 30px;
  outline: none;
  background: transparent;
  color: #bbbbbb;
  font-size: 18px;
  font-weight: 300;
  line-height: 42px; }
  .NavButton:hover {
    border-color: #888888;
    color: #888888;
    text-decoration: none; }
  .NavButton > * {
    margin-right: auto;
    margin-left: auto; }

.NavButton--withText {
  color: #666666;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px; }

.NavButton--back {
  line-height: 40px; }
