/* set by base dialog */
body.is-inDialog {
  overflow: hidden; }

.Dialog {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, white 0%, rgba(255, 255, 255, 0.95) 50%, rgba(255, 255, 255, 0.9) 100%); }

.Dialog-contentWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }

/* If the animation duration is changed make sure to update the timeout value in Dialog's view! */
.Dialog.is-opening {
  animation: fade-and-scale-in 100ms ease-out; }

.Dialog.is-closing {
  /* "forwards" to keep the last keyframe's styles after animation ended */
  animation: fade-and-scale-out 80ms ease-in forwards; }

.Dialog.is-sticky .Dialog-closeBtn {
  display: none; }

.Dialog-closeBtn {
  position: fixed;
  z-index: 2;
  top: 28px;
  right: 40px; }

.Dialog-backBtn {
  position: fixed;
  top: 28px;
  left: 40px; }

.Dialog-content {
  width: 100%; }

.Dialog-content--expanded {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-around;
  width: 100%;
  height: 100%; }

.Dialog-header--expanded {
  flex-grow: 1; }

.Dialog-body--expanded {
  flex-grow: 100;
  background-color: #f9f9f9; }

.Dialog-footer--expanded {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #f9f9f9; }

.Dialog-content.is-newContent {
  animation: fade-and-scale-in 100ms ease-out; }

.Dialog-header {
  padding: 28px 0;
  text-align: center; }

.Dialog-body {
  width: 940px;
  margin: 0 auto;
  padding-top: 28px;
  padding-bottom: 28px;
  border-top: 1px solid #dddddd; }

.Dialog-body.Dialog-body--tall {
  padding-top: 40px;
  padding-bottom: 40px; }

.Dialog-body.Dialog-body--noPaddingTop {
  padding-top: 0; }

.Dialog-body.Dialog-body--withoutBorder {
  border-top: none; }

.Dialog-body.Dialog-body--create {
  position: relative;
  width: 100%;
  margin-bottom: 104px;
  overflow: auto; }

.Dialog-body.Dialog-body--share {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 0; }
  .Dialog-body.Dialog-body--share .u-inner {
    width: 620px; }

.Dialog-body.Dialog-body--small {
  width: 670px; }

.Dialog-Stretcher {
  display: flex;
  justify-content: center;
  width: 940px; }

.Dialog-Stretcher--small {
  width: 700px; }

.Dialog-Stretcher--medium {
  width: 830px; }

.Filters.Dialog-bodyFilters {
  position: relative;
  background: none; }

.Filters.Dialog-bodyFilters {
  position: relative;
  background: none; }

.Dialog-body .ListingContent {
  margin-bottom: 100px; }

.Dialog-footer {
  padding: 28px 0;
  text-align: center; }

.Dialog-footer--withoutBorder {
  border-top: none; }

.Dialog-footer--simple {
  display: flex;
  justify-content: center; }

.Dialog-footer--small {
  width: 670px; }

.Dialog-footerContent {
  display: flex;
  justify-content: center; }

.Dialog-footerWarning {
  color: #f15743; }

.Dialog-preFooter {
  position: fixed;
  z-index: 3;
  right: 5%;
  bottom: 100px;
  left: 0;
  left: 5%;
  width: 90%;
  background: #f9f9f9; }
  .Dialog-preFooter::before {
    content: '';
    display: block;
    position: absolute;
    z-index: 2;
    top: -18px;
    width: 100%;
    height: 18px;
    background-image: linear-gradient(to bottom, rgba(249, 249, 249, 0), #f9f9f9); }

.Dialog-headerIcon {
  position: relative;
  width: 40px;
  height: 40px;
  margin-right: auto;
  margin-bottom: 12px;
  margin-left: auto;
  border: 1px solid rgba(0, 0, 0, 0.06);
  border-radius: 3px;
  font-size: 22px;
  line-height: 44px; }

.Dialog-headerIcon--neutral {
  border-color: #666666 !important; }
  .Dialog-headerIcon--neutral .CDB-IconFont {
    color: #666666 !important; }

.Dialog-headerIcon--twitter {
  border-color: #55ACEE;
  color: #55ACEE; }

.Dialog-headerIcon--negative {
  border-color: #F15743;
  color: #F15743; }

.Dialog-headerIcon--positive {
  border-color: #8fb83f;
  color: #8fb83f; }

.Dialog-headerIcon--alert {
  border-color: orange;
  color: orange; }

.Dialog-headerIcon--small {
  position: relative;
  top: -3px;
  font-size: 10px;
  vertical-align: middle; }

.Dialog-headerIconBadge {
  /* "backwards" to use the first keyframe's styles before animation starts */
  animation: fade-and-bounce-up 0.6s 350ms ease-in-out backwards; }

.Dialog-headerTitle {
  color: #666666;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

.Dialog-headerTitle--warning {
  color: orange; }

.Dialog-headerTitle--error {
  color: #f15743; }

.Dialog-headerText {
  color: #666666;
  font-size: 15px;
  font-weight: 300;
  line-height: 22px; }

.Dialog-headerText:not(:last-child) {
  margin-bottom: 12px; }

.Dialog-headerText--centered {
  margin: auto; }

.Dialog-headerText--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.Dialog-headerText--small {
  width: 670px; }

.Dialog-footerBtn {
  margin-right: 20px; }

.Dialog-affectedEntities {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.Dialog-content--expanded {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-around;
  width: 100%;
  height: 100%; }

.Dialog-header--expanded {
  flex-grow: 1;
  min-height: 100px; }

.Dialog-expandedSubContent {
  flex-grow: 100;
  margin-bottom: 97px;
  overflow-x: hidden;
  overflow-y: auto;
  border-top: 1px solid #dddddd;
  background-color: #f9f9f9; }

.Dialog-expandedSubContentInner {
  position: relative; }

.Dialog-bodyInnerExpandedWithSubFooter {
  padding-bottom: 97px; }

.Dialog-bodyInnerExpandedWithPreFooter {
  padding-bottom: 197px; }

.Dialog-stickyFooter {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 97px;
  background-color: #f9f9f9; }

.Dialog-contentSubHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-top: 28px;
  padding-bottom: 28px;
  border-bottom: 1px solid #dddddd; }

.Dialog-resultsBody {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 620px; }

.Dialog-resultsBody--vcenter {
  align-items: center; }

.Dialog-resultsBodyIcon {
  margin-right: 20px; }

.Dialog-resultsBodyTexts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 520px; }

.Dialog-narrowerContent {
  width: 620px; }

.Dialog-countryBack {
  margin-left: 12px;
  float: right;
  pointer-events: auto; }

@keyframes fade-and-scale-in {
  0% {
    transform: scale(2);
    opacity: 0; }
  10% {
    transform: scale(1.1);
    opacity: 0.9; }
  100% {
    transform: scale(1);
    opacity: 1; } }

@keyframes fade-and-bounce-up {
  0% {
    transform: translateY(6px);
    opacity: 0; }
  15% {
    transform: translateY(-3px);
    opacity: 0.8; }
  45% {
    transform: translateY(2px);
    opacity: 1; }
  70% {
    transform: translateY(-1px); }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fade-and-scale-out {
  0% {
    transform: scale(1);
    opacity: 1; }
  80% {
    transform: scale(1.4);
    opacity: 0.05; }
  100% {
    transform: scale(2);
    opacity: 0; } }

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
  .Dialog-body .ListingContent {
    margin-bottom: 200px; } }
