/* FONT FACE */
.TabsPanel > .TabLink {
  border-radius: 0; }
  .TabsPanel > .TabLink:first-child {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px; }
  .TabsPanel > .TabLink:last-child {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px; }

.TabLink {
  display: block;
  box-sizing: content-box;
  width: 138px;
  height: 38px;
  padding: 0;
  border: 1px solid #dddddd;
  border-radius: 4px;
  color: #999999;
  font-size: 14px;
  line-height: 38px; }
  .TabLink:hover {
    border-color: #bbd7f2;
    background: #FFF;
    color: #666666;
    text-decoration: none; }

.TabLink:hover {
  border-color: #bbd7f2; }

.is-selected > .TabLink,
.is-selected > .TabLink:hover,
.TabLink.selected,
.TabLink.selected:hover {
  border-color: #dddddd;
  background: #FFF;
  color: #666666;
  cursor: default; }

.TabLink.TabLink--positive.selected,
.TabLink.TabLink--positive.selected:hover {
  border-color: #bbd7f2; }

.TabLink.disabled {
  opacity: 0.5; }
  .TabLink.disabled:hover {
    border-color: #dddddd;
    background: transparent;
    color: #999999;
    text-decoration: none;
    cursor: default; }

.TabLink.TabLink--textCenterUpcase {
  color: #666666;
  text-align: center;
  text-transform: uppercase; }
  .TabLink.TabLink--textCenterUpcase.selected, .TabLink.TabLink--textCenterUpcase.selected:hover {
    border-color: #9dc2e0;
    background: #eff6fc;
    color: #666666; }

.TabLink.arcgis {
  width: 160px; }

.TabLink.box {
  display: block;
  width: auto;
  height: auto;
  margin: 0; }

.TabIcon {
  display: inline-block;
  height: 20px;
  margin: 0 6px 3px 0;
  background: url("/assets/1.0.0-assets.136/images/layout/new_import_icons.png") no-repeat 0 0;
  vertical-align: middle; }
  @media (-webkit-min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
    .TabIcon {
      /* on retina, use image that's scaled by 2 */
      background: url("/assets/1.0.0-assets.136/images/layout/new_import_icons@2x.png");
      background-position: 0 0;
      background-size: 223px 40px; } }

