@charset "UTF-8";
.Checkbox {
  display: flex;
  align-items: center; }

.Checkbox-input {
  position: relative;
  width: 16px;
  height: 16px;
  border: 2px solid #dddddd;
  border-radius: 3px;
  outline: none;
  background: white; }
  .Checkbox-input:hover {
    border-color: #AAA; }

.Checkbox-input:focus {
  border-color: #AAA; }

.Checkbox-input.is-checked::after {
  content: "\2714";
  position: absolute;
  top: -3px;
  left: 1px;
  color: #0090D7;
  font-size: 16px; }

.Checkbox-label {
  margin-left: 8px;
  color: #999999; }

.Checkbox.is-disabled .Checkbox-input {
  opacity: 0.4; }
  .Checkbox.is-disabled .Checkbox-input:hover {
    border-color: #dddddd;
    cursor: default; }
  .Checkbox.is-disabled .Checkbox-input:focus {
    border-color: #dddddd; }

.Checkbox.is-disabled .Checkbox-label {
  opacity: 0.4; }

.Checkbox-strong {
  color: #2E3C43; }
