/* FONT FACE */
.SupportBanner {
  display: block;
  width: 100%;
  padding: 20px 0 40px; }

.SupportBanner-inner {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: space-between; }

.SupportBanner-link {
  display: block; }

.SupportBanner-info {
  width: 640px; }

.SupportBanner-infoDescription {
  margin-top: 4px; }

.SupportBanner-infoDescriptionLink {
  color: #999999;
  text-decoration: underline; }
  .SupportBanner-infoDescriptionLink:hover {
    color: #666666; }
