.PricePlans {
  display: inline-block;
  width: 100%; }

.PricePlans-options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0 15px; }

.PricePlans-yearly {
  display: flex; }

.PricePlans-yearlyText {
  margin-left: 8px;
  color: #636d72;
  font-size: 15px; }

.PricePlans-yearlyText--highlight {
  color: #8fb83f;
  font-style: normal; }

.PricePlans-list {
  display: flex;
  justify-content: space-between;
  padding: 0;
  border: 1px solid #dddddd;
  border-radius: 4px;
  background: #FFF; }

.PricePlans-item {
  width: 100%;
  padding: 20px 0 10px; }

.PricePlans-item:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px; }

.PricePlans-item:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px; }

.PricePlans-item.is-highlighted {
  background: #F6FBFE; }

.PricePlans-itemHeader {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 85px;
  text-align: center; }

.PricePlans-itemTitle {
  color: #2e3c43;
  font-size: 13px;
  text-transform: uppercase; }

.PricePlans-itemPrice {
  margin-top: 10px;
  color: #2e3c43;
  font-size: 14px; }

.PricePlans-itemPriceFigure {
  font-size: 32px; }

.PricePlans-itemPriceRest {
  color: #979ea1; }

.PricePlans-itemPriceException {
  margin-top: 5px;
  color: #979ea1;
  font-size: 12px; }

.PricePlans-button {
  display: block;
  width: 140px;
  margin: 0 auto;
  line-height: 38px; }

.PricePlans-button--grey {
  display: block;
  background-color: #cccccc;
  color: #FFF; }

.PricePlans-itemContent {
  padding: 0 30px; }

.PricePlans-itemContentAttr {
  display: block;
  padding: 10px 0;
  color: #2e3c43;
  font-size: 13px;
  text-align: center; }

.PricePlans-itemContentAttr.is-disabled {
  color: rgba(102, 102, 102, 0.4); }

.PricePlans-itemContentAttr.is-highlighted {
  color: #333333;
  font-weight: 700; }

.PricePlans-trialText {
  color: #636d72;
  font-size: 15px; }

.PricePlans-trialIcon {
  margin-left: 3px;
  color: #0090D7; }
