.DefaultParagraph {
  color: #666666;
  font-size: 15px;
  font-weight: 300;
  line-height: 22px; }

.DefaultParagraph--secondary {
  color: #999999; }

.DefaultParagraph--error {
  color: #f15743; }
  .DefaultParagraph--error .Button--link {
    color: #f15743;
    text-decoration: underline; }

.DefaultParagraph--tertiary {
  color: #999999;
  font-size: 13px;
  font-weight: 300;
  line-height: 20px; }

.DefaultParagraph--short {
  width: 500px; }

.DefaultParagraph--centered {
  margin: 0 auto; }

.DefaultParagraph--spaced {
  margin-top: 10px;
  margin-bottom: 10px; }

.DefaultParagraph--normalSize {
  font-size: 13px;
  line-height: 20px; }

@media (min-width: 400px) {
  .DefaultParagraph--short {
    width: 200px; } }

@media (min-width: 760px) {
  .DefaultParagraph--short {
    width: 500px; } }
