/* FONT FACE */
/**
  *  Progress element styles
  *
  *  It needs:
  *  - Height of the progress "bar".
  *  - Border radius of the element.
  *  - If it needs a white gradient over it.
  *  - If bars needs a min width.
  *
  */
/* FONT FACE */
.SideMenu {
  display: block;
  z-index: 10;
  width: 260px;
  min-height: 600px;
  background: transparent; }

.SideMenu-progress {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 30px 0; }

.SideMenu-progressBar {
  width: 140px; }
  .SideMenu-progressBar div.progress-bar {
    display: block;
    position: relative;
    width: 100%;
    height: 6px;
    overflow: hidden;
    border-radius: 9px;
    background: #F5F5F5; }
    .SideMenu-progressBar div.progress-bar.overflow-visible {
      overflow: visible; }
    .SideMenu-progressBar div.progress-bar .bar-3,
    .SideMenu-progressBar div.progress-bar .bar-2,
    .SideMenu-progressBar div.progress-bar .bar-1,
    .SideMenu-progressBar div.progress-bar .bar-0 {
      position: absolute;
      top: 0;
      bottom: 0;
      border-radius: 9px; }
    .SideMenu-progressBar div.progress-bar .bar-3 {
      z-index: 3;
      border-right: 1px solid #FFF;
      background: #7DD4FF; }
    .SideMenu-progressBar div.progress-bar .bar-2 {
      z-index: 2;
      background: #0090D7; }
    .SideMenu-progressBar div.progress-bar .bar-1 {
      z-index: 1;
      background: #E5E5E5; }
      .SideMenu-progressBar div.progress-bar .bar-1::after {
        display: none; }
      .SideMenu-progressBar div.progress-bar .bar-1::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        border-right: 1px solid white; }
    .SideMenu-progressBar div.progress-bar .bar-0 {
      background: url("/assets/1.0.0-assets.136/images/layout/unassigned_bkg.png") repeat-x left 0;
      z-index: 0; }
      .SideMenu-progressBar div.progress-bar .bar-0::after {
        display: none; }
    .SideMenu-progressBar div.progress-bar .danger {
      background: #f15743; }
    .SideMenu-progressBar div.progress-bar .caution {
      background: #f8b85c; }

.SideMenu-separator {
  display: block;
  position: relative;
  z-index: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.1); }

.SideMenu-list {
  align-items: center;
  justify-content: space-between; }

.SideMenu-typeItem {
  position: relative;
  margin-bottom: 20px; }
  .SideMenu-typeItem:first-child {
    margin-left: 0; }
  .SideMenu-typeItem:last-child {
    margin-right: 0; }

.SideMenu-typeLink {
  display: inline-block;
  border: none;
  outline: none;
  background: transparent;
  color: #1181FB;
  font-size: 14px; }
  .SideMenu-typeLink:hover {
    text-decoration: underline; }

.SideMenu-typeLink.is-selected {
  color: #333333;
  font-weight: bold; }
  .SideMenu-typeLink.is-selected:hover {
    text-decoration: none;
    cursor: default; }

.SideMenu-typeLink.is-disabled {
  color: rgba(0, 0, 0, 0.3); }
  .SideMenu-typeLink.is-disabled:hover {
    color: rgba(0, 0, 0, 0.3);
    text-decoration: none;
    cursor: default; }

.SideMenu-typeLink.is-disabled {
  color: rgba(0, 0, 0, 0.3); }
  .SideMenu-typeLink.is-disabled:hover {
    color: rgba(0, 0, 0, 0.3);
    text-decoration: none;
    cursor: default; }

.SideMenu-separator {
  display: flex;
  width: 240px;
  height: 1px;
  margin: 20px 0;
  background-color: rgba(0, 0, 0, 0.1); }
