/* FONT FACE */
.HighlightIcon {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  color: white;
  font-size: 13px;
  line-height: 20px !important;
  text-align: center;
  vertical-align: top; }

.HighlightIcon.HighlightIcon--warning {
  border: 1px solid #F2C000;
  background: #F6D35C; }
