.RadioButton {
  display: flex;
  align-items: center; }

.RadioButton-input {
  position: relative;
  width: 16px;
  height: 16px;
  border: 2px solid #dddddd;
  border-radius: 16px;
  outline: none;
  background: #FFF; }
  .RadioButton-input:hover {
    border-color: #AAA; }

.RadioButton-input.is-checked::before {
  content: '';
  position: absolute;
  top: 1px;
  left: 1px;
  width: 10px;
  height: 10px;
  border-radius: 12px;
  background: #4A90E2; }

.RadioButton-input.is-disabled {
  cursor: default; }
  .RadioButton-input.is-disabled::before {
    background: #DDD;
    cursor: default; }

.RadioButton-label {
  margin-left: 8px;
  color: #999999; }

.RadioButton-label.dark-color {
  color: #333333; }

.RadioButton.is-checked .RadioButton-label {
  color: #333333; }

.RadioButton.is-alert .RadioButton-label {
  color: #ea703d; }

.RadioButton.is-alert .RadioButton-input {
  border-color: #ea703d; }
  .RadioButton.is-alert .RadioButton-input::before {
    background: #ea703d; }

.RadioButton.is-warning .RadioButton-label {
  color: #c67b44; }

.RadioButton.is-warning .RadioButton-input {
  border-color: #c67b44; }
  .RadioButton.is-warning .RadioButton-input::before {
    background: #c67b44; }

.RadioButton.is-disabled .RadioButton-input {
  opacity: 0.4; }
  .RadioButton.is-disabled .RadioButton-input:hover {
    border-color: #dddddd;
    cursor: default; }
  .RadioButton.is-disabled .RadioButton-input:focus {
    border-color: #dddddd; }

.RadioButton.is-disabled .RadioButton-label {
  opacity: 0.4; }

.RadioButtonInput {
  position: absolute;
  z-index: 1;
  width: 20px;
  height: 20px;
  opacity: 0;
  vertical-align: middle;
  cursor: pointer; }

.RadioButtonInput-face {
  position: relative;
  width: 16px;
  height: 16px;
  border: 2px solid #dddddd;
  border-radius: 50%;
  outline: none;
  background: #FFF;
  cursor: pointer; }
  .RadioButtonInput-face::before {
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    width: 10px;
    height: 10px;
    border-radius: 50%; }

.RadioButtonInput:checked + .RadioButtonInput-face::before {
  content: '';
  position: absolute;
  top: 3px;
  left: 3px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #4A90E2; }

.RadioButtonInput:disabled + .RadioButtonInput-face {
  opacity: 0.4; }
  .RadioButtonInput:disabled + .RadioButtonInput-face:focus {
    border-color: #dddddd; }
  .RadioButtonInput:disabled + .RadioButtonInput-face:hover {
    border-color: #dddddd;
    cursor: default; }

.RadioButtonInput:hover + .RadioButtonInput-face {
  border-color: #AAA; }
