/* FONT FACE */
.u-transparent {
  opacity: 0; }

.u-inner {
  margin: 0 auto;
  padding: 0 12px; }

.u-capitalize {
  text-transform: capitalize; }

.u-breakLongWords {
  word-break: break-word; }

.u-ellipsLongText {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.u-hide,
.u-showOnMobile,
.u-showOnTablet {
  display: none !important; }

.u-disabled {
  opacity: 0.4;
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
  /* Firefox 10+, Firefox on Android */ }

.u-iblock {
  display: inline-block; }

.u-txt-center {
  text-align: center; }

.u-txt-left {
  text-align: left; }

.u-txt-right {
  text-align: right; }

.u-whiteSpace-nowrap {
  white-space: nowrap; }

.u-valign::before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle; }

.u-malign {
  vertical-align: middle; }

.u-vspace-xxl {
  margin-bottom: 120px; }

.u-vspace-xl {
  margin-bottom: 60px; }

.u-vspace-l {
  margin-bottom: 40px; }

.u-vspace-m {
  margin-bottom: 24px; }

.u-vspace-s {
  margin-bottom: 12px; }

.u-vspace-xs {
  margin-bottom: 8px; }

.u-tspace-xl {
  margin-top: 60px; }

.u-tspace-l {
  margin-top: 40px; }

.u-tspace-m {
  margin-top: 24px; }

.u-tspace-s {
  margin-top: 12px; }

.u-tspace-xs {
  margin-top: 8px; }

.u-rspace-xl {
  margin-right: 60px; }

.u-rspace-l {
  margin-right: 40px; }

.u-rspace-m {
  margin-right: 24px; }

.u-rspace-s {
  margin-right: 12px; }

.u-rspace-xs {
  margin-right: 8px; }

.u-tspace--20 {
  margin-top: 20px; }

.u-rspace--20 {
  margin-right: 20px; }

.u-vspace--20 {
  margin-bottom: 20px; }

.u-lspace-28 {
  margin-left: 28px; }

.u-lspace-23 {
  margin-left: 23px; }

.u-tupper {
  text-transform: uppercase; }

.u-border-bottom {
  box-shadow: 0px 1px 0px #dddddd; }

@media (min-width: 960px) {
  .u-inner {
    width: 940px;
    padding: 0; } }

@media (max-width: 760px) {
  .u-hideOnMobile {
    display: none !important; }
  .u-showOnMobile {
    display: inline-block !important; } }

@media (max-width: 960px) {
  .u-hideOnTablet {
    display: none !important; }
  .u-showOnTablet {
    display: inline-block !important; } }

@media (min-width: 761px) {
  .u-block--mobile {
    display: none !important; } }

@media (max-width: 760px) {
  .u-mobile-hide {
    display: none !important; }
  .u-block--mobile {
    display: block !important; } }
