.Title {
  font-weight: 400; }

.Title--l {
  font-size: 48px;
  line-height: 58px; }

.Title--m {
  font-size: 34px;
  line-height: 48px; }

.Title--s {
  font-size: 26px;
  line-height: 33px; }

.Title--xs {
  font-size: 20px;
  line-height: 30px; }

.Title--light {
  font-weight: 300; }

.Title--bold {
  font-weight: 500; }

.Title--dark {
  color: #333; }

.Title--grey {
  color: #CCC; }

.Title--white {
  color: #FFF; }

.Title--em {
  color: rgba(255, 255, 255, 0.6);
  font-size: 18px;
  font-style: italic;
  font-weight: 200;
  line-height: 1; }

.Title--bold {
  font-weight: 500; }

.Title-small {
  margin-bottom: 12px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 19px;
  text-transform: uppercase; }

@media (max-width: 960px) {
  .Title--l {
    font-size: 36px;
    line-height: 42px; } }

@media (max-width: 400px) {
  .Title--l {
    font-size: 34px;
    line-height: 40px; } }
