.Header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 80px;
  background: #1785FB; }

.Header-inner {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: space-between; }

.Header-navigationList {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.Header-navigationBreadcrumb {
  display: flex;
  align-items: center;
  margin-left: 14px; }

.Header-navigationBreadcrumbItem {
  padding-left: 6px;
  color: rgba(255, 255, 255, 0.6); }

.Header-navigationBreadcrumbParagraph {
  display: inline-block;
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: bottom;
  white-space: nowrap; }

.Header-navigationItem {
  margin-left: 24px; }

.Header-navigationLink {
  transition: all 0.15s;
  color: #FFF;
  font-size: 13px;
  font-weight: 700;
  line-height: 27px; }
  .Header-navigationLink:hover {
    color: rgba(255, 255, 255, 0.6);
    text-decoration: none; }

.Header-navigationBreadcrumbItem:first-child {
  padding-left: 0; }

.Header-navigationBreadcrumbLink {
  color: #FFF; }
  .Header-navigationBreadcrumbLink.is-disabled {
    color: rgba(255, 255, 255, 0.8);
    cursor: default; }

.Header-settingsList {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

.Header-settingsItem {
  position: relative;
  margin-left: 24px;
  text-transform: uppercase; }

.Header-settingsLink--public {
  font-size: 13px;
  font-weight: 700;
  text-transform: none; }

.Header-settingsLink {
  transition: all 0.2s;
  color: #FFF; }
  .Header-settingsLink:hover {
    color: rgba(255, 255, 255, 0.6);
    text-decoration: none; }

.Header-settingsLink.Header-settingsLink--dashboard {
  color: #FFF; }

.Header-settingsLink.Header-settingsLink--dashboard:hover {
  text-decoration: underline; }

.Header-darrow {
  position: relative;
  top: -1px;
  font-size: 6px; }

@media (min-width: 400px) {
  .Header-logoText {
    margin-left: 12px; } }

@media only screen and (max-width: 360px) {
  .Header .Header-inner {
    height: 64px; }
  .Header {
    height: 64px; }
  .Header-settingsItem--avatar {
    height: 32px; }
  .Header .Header-settingsList .UserAvatar-img--medium {
    width: 32px;
    height: 32px; }
  .Header .Header-settingsList .Button {
    height: 32px;
    padding: 0 20px; }
  .Header .Logo--avatar {
    width: 32px;
    height: 32px;
    line-height: 45px; }
  .Header .Logo--avatar i {
    font-size: 22px; } }
