/* FONT FACE */
.MapsGallery {
  width: 940px;
  margin: auto;
  padding: 40px 0;
  clear: both;
  overflow: hidden;
  border-top: 1px solid #DDD; }

.PecanMap-MapsList {
  display: block;
  position: relative;
  width: 5000px;
  height: 247px;
  margin: 0;
  padding: 0;
  clear: both;
  overflow: hidden;
  transition: margin 150ms; }

.PecanMap-MapsList.is--centered {
  margin: auto;
  transition: none; }

.MapCard-content--small {
  height: 35px;
  padding-top: 18px; }

.PecanMap-MapsList .DefaultTitle,
.PecanMap-MapsList .DefaultTitle a {
  color: #999;
  font-weight: 300; }

.PecanMap-MapsList .DefaultTitle strong {
  color: #666;
  font-weight: normal; }

.PecanMap-MapsList .DefaultTitle a:hover {
  color: #666; }

.PecanMap-navigation {
  position: absolute;
  z-index: 100000;
  width: 940px; }

.PecanMap-navigation.is-hidden {
  display: none; }

.PecanMap-navigationButton {
  position: absolute;
  top: 90px;
  padding: 0;
  transition: opacity 150ms;
  line-height: 40px !important; }

.PecanMap-navigationButton--prev {
  left: -80px; }

.PecanMap-navigationButton--next {
  right: -80px; }

.PencaMap-navigationButton.is-disabled:hover {
  border-color: #CCC;
  color: #CCC; }

.GalleryList-item.MapsList-item {
  display: inline-block;
  margin: 0 20px 0 0;
  vertical-align: top; }
  .GalleryList-item.MapsList-item:last-child {
    margin: 0; }

.Pecan-TorqueWirzardIcon {
  background: url("/assets/1.0.0-assets.136/images/layout/pecan_torque_wizard.png") center center;
  position: absolute;
  z-index: 10;
  top: 49px;
  left: 50%;
  width: 84px;
  height: 84px;
  margin-left: -42px; }

.PecanCard-footer {
  position: relative;
  margin: 0;
  padding: 0;
  border-top: 0; }

.NullCount {
  color: #999;
  font-size: 11px;
  font-style: italic;
  line-height: 0; }

.CategoryList {
  margin: 0;
  padding: 0;
  float: left;
  line-height: 0; }

.CategoryList .CategoryList-item {
  display: inline-block;
  width: 5px;
  height: 5px;
  margin: 0 2px 0 0;
  border-radius: 50%;
  background: #DDD;
  vertical-align: top; }

.CategoryList .CategoryList-item:last-child {
  margin: 0; }
