/* FONT FACE */
.ContentResult {
  width: 460px;
  min-height: 360px;
  margin: 0 auto;
  padding-top: 160px;
  text-align: center; }

.ContentResult.is-empty {
  width: auto;
  min-height: 0;
  margin: 0 auto;
  padding-top: 0; }

.ContentResult.no-datasets {
  position: relative;
  z-index: 9;
  width: auto;
  height: 232px;
  min-height: 0;
  margin: 0 auto;
  padding: 28px 0 0;
  overflow: hidden;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06); }
  .ContentResult.no-datasets::after, .ContentResult.no-datasets::before {
    content: '';
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0; }
  .ContentResult.no-datasets .NoDatasets-illustration {
    position: absolute;
    bottom: -52px;
    left: 50%;
    width: 671px;
    height: 195px;
    margin-left: -335px;
    background-image: url("/assets/1.0.0-assets.136/images/layout/search_lens_bkg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%; }
  .ContentResult.no-datasets .NoDatasets-illustration--secondary {
    background: url("/assets/1.0.0-assets.136/images/layout/connect-dataset.png") center center no-repeat; }

.ContentResult-smallLoader {
  position: absolute;
  z-index: 10;
  top: -2px;
  right: 0;
  left: 0;
  width: 100%;
  height: 2px;
  overflow: hidden;
  background: transparent; }
  .ContentResult-smallLoader::before {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    width: 600px;
    height: 2px;
    animation: pulsate 1.5s linear infinite;
    background: rgba(0, 0, 0, 0.55); }

.ContentResult-icon {
  display: inline-block;
  width: 38px;
  height: 38px;
  border: 1px solid #dddddd;
  border-radius: 2px;
  color: #cccccc;
  font-size: 22px;
  line-height: 36px;
  vertical-align: top; }

.ContentResult-iconNegative {
  border-color: #f15743;
  color: #f15743; }

.ContentResult-iconPositive {
  border-color: #8fb83f;
  color: #8fb83f; }

.ContentResult-iconAlert {
  border-color: #c67b44;
  color: #c67b44; }

.ContentResult-title {
  display: block;
  margin: 15px 0 10px;
  color: #666666;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px; }

.ContentResult-text {
  display: block;
  color: #666666;
  font-size: 15px;
  font-weight: 300; }

.ContentResult-textTitle {
  color: #3aa9e3;
  text-decoration: underline; }
  .ContentResult-textTitle:hover {
    color: #227dbd; }

@keyframes pulsate {
  0% {
    right: 100%; }
  50% {
    right: 0%; }
  100% {
    right: -100%; } }
