.DatasetCard {
  position: relative;
  width: 288px;
  margin: 0 auto;
  border: 1px solid #DBDBDB;
  border-radius: 4px;
  background-color: white; }

.DatasetCard-title {
  width: 100%;
  margin: 0 0 3px; }

.DatasetCard-content {
  display: flex;
  position: relative;
  z-index: 1;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 17px 20px 16px;
  transition: opacity 250ms;
  border-radius: 4px;
  opacity: 1; }

.DatasetCard-contentFooter {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between; }

.DatasetCard-contentFooter > *:not(:last-child) {
  margin-right: 20px; }

.DatasetCard-contentBody {
  width: 100%; }

.DatasetCard-contentBodyRow {
  margin-bottom: 4px; }

.DatasetCard-contentBodyRow--flex {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  width: 100%; }

.DatasetCard-contentBodyDetails--left {
  display: flex; }

.DatasetCard-contentBodyDetails--right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%; }

@keyframes pulsate {
  0% {
    right: 100%; }
  50% {
    right: 0%; }
  100% {
    right: -100%; } }

@media (min-width: 760px) {
  .DatasetCard {
    width: 298px; } }

@media only screen and (max-width: 544px) {
  .DatasetCard-contentFooter > *:not(:last-child) {
    margin-right: 10px; } }
