.UserAvatar {
  display: flex;
  align-items: center; }

.UserAvatar.is-in-list {
  margin-left: 4px; }

.UserAvatar.is-error::before {
  content: 'x';
  display: flex;
  position: absolute;
  top: -8px;
  right: -8px;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border-radius: 30px;
  background-color: #f15743;
  color: #FFF;
  font-size: 12px;
  text-align: center; }

.UserAvatar-img {
  border-right: 0;
  border-left: 0;
  background: #FFF; }

.UserAvatar-img--no-src {
  background-image: url("/assets/1.0.0-assets.136/images/avatars/avatar_ghost_red.png"); }

.UserAvatar-img--textReplacement {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #3aa9e3;
  font-size: 11px; }
  .UserAvatar-img--textReplacement:hover {
    border-color: #3aa9e3; }

.UserAvatar-img.is-error {
  border-radius: 4px;
  border-color: #f15743; }

.UserAvatar-img--large {
  width: 96px;
  height: 96px;
  border-radius: 4px;
  background-size: 96px; }

.UserAvatar-img--big {
  width: 60px;
  height: 60px;
  border-radius: 4px;
  background-size: 60px; }

.UserAvatar-img--medium-large {
  width: 42px;
  height: 42px;
  border-radius: 4px;
  background-size: 42px; }

.UserAvatar-img--medium {
  width: 36px;
  height: 36px;
  border-radius: 4px;
  background-size: 36px; }

.UserAvatar-img--small {
  width: 20px;
  height: 20px;
  border-radius: 2px;
  background-size: 20px; }

.UserAvatar-img--smaller {
  width: 16px;
  height: 16px;
  border-radius: 2px;
  background-size: 16px; }

.UserAvatar-moreItems {
  display: flex;
  align-items: baseline;
  justify-content: center;
  border-radius: 4px;
  box-shadow: 0 0 0 1px #cccccc inset;
  color: #cccccc;
  font-size: 26px;
  font-weight: 500;
  line-height: 26px; }

.UserAvatar-moreItems::before {
  content: '...'; }
