/* FONT FACE */
.Grid {
  display: flex; }

.Grid-inner {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  margin-right: -20px;
  margin-left: -20px; }

.Grid-inner--hcenter {
  justify-content: center; }

.Grid-inner--vcenter {
  align-items: center; }

.Grid-inner--bottom {
  margin-bottom: -20px; }

.Grid-cell {
  padding-right: 20px;
  padding-left: 20px; }

.Grid-inner--nopadding {
  margin-right: -5px;
  margin-left: -5px; }
  .Grid-inner--nopadding > .Grid-cell {
    padding-right: 5px;
    padding-left: 5px; }

.Grid-inner--between {
  align-items: center;
  justify-content: space-between; }

.Grid-inner--shortPadding {
  margin-right: -10px;
  margin-left: -10px; }
  .Grid-inner--shortPadding > .Grid-cell {
    padding-right: 10px;
    padding-left: 10px; }

.Grid-cell--col1 {
  flex: 0 0 8.3334%;
  max-width: 8.3334%; }

.Grid-cell--col2 {
  flex: 0 0 16.6667%;
  max-width: 16.6667%; }

.Grid-cell--5cols {
  flex: 0 0 20%;
  max-width: 20%; }

.Grid-cell--col3 {
  flex: 0 0 25%;
  max-width: 25%; }

.Grid-cell--col4 {
  flex: 0 0 33.3334%;
  max-width: 33.3334%; }

.Grid-cell--col5 {
  flex: 0 0 41.6665%;
  max-width: 41.6665%; }

.Grid-cell--col6 {
  flex: 0 0 50%;
  max-width: 50%; }

.Grid-cell--col7 {
  flex: 0 0 58.3331%;
  max-width: 58.331%; }

.Grid-cell--col8 {
  flex: 0 0 66.6666%;
  max-width: 66.6666%; }

.Grid-cell--col9 {
  flex: 0 0 75%;
  max-width: 75%; }

.Grid-cell--col10 {
  flex: 0 0 83.33%;
  max-width: 83.33%; }

.Grid-cell--col11 {
  flex: 0 0 91.6666%;
  max-width: 91.6666%; }

.Grid-cell--col12 {
  flex: 100%;
  max-width: 100%; }

@media (max-width: 960px) {
  .Grid-cell--col3--tablet {
    flex: 0 0 25%;
    max-width: 25%; }
  .Grid-cell--col6--tablet {
    flex: 0 0 50%;
    max-width: 50%; }
  .Grid-cell--col4--tablet {
    flex: 0 0 33.3334%;
    max-width: 33.3334%; }
  .Grid-cell--col12--tablet {
    flex: 0 0 100%;
    max-width: 100%; } }

@media (max-width: 760px) {
  .Grid-cell--col8--mobile {
    flex: 0 0 66.6666%;
    max-width: 66.6666%; }
  .Grid-cell--col6--mobile {
    flex: 0 0 50%;
    max-width: 50%; }
  .Grid-cell--col12--mobile {
    flex: 0 0 100%;
    max-width: 100%; } }

@media (max-width: 400px) {
  .Grid-cell--col12--small,
  .Grid-cell--col6--mobile {
    flex: 0 0 100%;
    max-width: 100%; } }
