/* FONT FACE */
.ColorPicker {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 3px; }
  .ColorPicker::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 38px;
    height: 38px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 3px; }

.UserSettings-ColorPicker {
  display: inline-block;
  position: relative;
  width: 100px;
  height: 40px;
  border-radius: 3px; }
  .UserSettings-ColorPicker::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 98px;
    height: 38px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 3px; }
