.ImportOptions {
  position: relative;
  width: 780px;
  margin: 0 auto 20px; }

.ImportOptions-navigationButton {
  position: absolute;
  top: 28px;
  padding: 0;
  line-height: 40px !important; }

.ImportOptions-navigationButton--prev {
  left: -80px; }

.ImportOptions-navigationButton--next {
  right: -80px; }

.ImportOptions-navigationButton.is-disabled:hover {
  border-color: #CCC;
  color: #CCC; }

.ImportOptions-tabs {
  display: block;
  width: 780px;
  padding: 28px 0;
  overflow-x: hidden; }

.ImportOptions-tabsList {
  display: flex;
  flex-wrap: nowrap;
  transition: margin 150ms; }

.ImportOptions-tab {
  min-width: 140px;
  margin: 0 20px 0 0;
  text-align: center; }
