.Button {
  display: flex;
  align-items: center;
  height: 40px;
  padding: 0 29px;
  border: 1px solid white;
  border-radius: 4px;
  outline: none;
  text-align: center;
  text-decoration: none; }

.Button--inline {
  display: inline-block;
  margin: 0 12px; }

.Button--centered {
  margin: auto; }

.Button,
.Button span {
  box-sizing: border-box;
  color: #FFF;
  font-size: 12px;
  font-weight: 700;
  line-height: 15px;
  text-transform: uppercase;
  white-space: nowrap; }

.Button-inner {
  line-height: 40px; }

.Button-arrowMenu {
  margin-top: 2px;
  margin-left: 3px;
  font-size: 8px;
  vertical-align: top; }

.Button.is-hidden {
  display: none; }

.Button:hover,
.Button span:hover {
  text-decoration: none; }

.Button.is-disabled {
  opacity: 0.5; }
  .Button.is-disabled:hover {
    cursor: default; }

.Button--main {
  border-color: #0090d7;
  background-color: #0090d7; }
  .Button--main:hover {
    background-color: #007fbd; }
  .Button--main.is-disabled:hover {
    background-color: #0090d7; }

.Button--positive {
  border-color: #8fb83f;
  background-color: #8fb83f; }
  .Button--positive:hover {
    border-color: #7fa538;
    background-color: #7fa538; }
  .Button--positive.is-disabled:hover {
    border-color: #8fb83f;
    background-color: #8fb83f; }

.Button--invert {
  border-color: #3aa9e3;
  background-color: none;
  color: #3aa9e3; }
  .Button--invert span {
    color: #3aa9e3; }
  .Button--invert:hover {
    background-color: #3aa9e3;
    color: #FFF; }
    .Button--invert:hover span {
      color: #FFF; }

.Button--negative {
  border-color: #f15743;
  background-color: #f15743; }
  .Button--negative:hover {
    background-color: #b93f37; }

.Button--alert {
  border-color: orange;
  background-color: orange; }
  .Button--alert:hover {
    background-color: orange; }

.Button--secondary {
  border: 1px solid #bbbbbb;
  background-color: white; }
  .Button--secondary span {
    color: #bbbbbb; }
  .Button--secondary .Button-arrowMenu {
    color: #777; }
  .Button--secondary:hover {
    border-color: #888888; }
    .Button--secondary:hover span {
      color: #888888; }
    .Button--secondary:hover .Button-arrowMenu {
      color: #444; }

.Button--secondaryTransparentBkg {
  background-color: transparent; }

.Button.is-smaller {
  height: 30px;
  padding: 0 15px; }

.Button--secondaryBlue {
  border-color: transparent;
  background-color: white; }
  .Button--secondaryBlue span {
    color: #0090d7; }
  .Button--secondaryBlue:hover span {
    color: #007fbd; }

.Button--light {
  border: 1px solid #FFF;
  background-color: white;
  color: #384047; }
  .Button--light:hover {
    background-color: #384047;
    color: #FFF; }

.Button--link {
  color: #3aa9e3;
  text-transform: inherit; }
  .Button--link:hover {
    color: #333333;
    text-decoration: underline; }

.Button--link.is-inline {
  padding-right: 0;
  padding-left: 0; }

.Button--link.is-destructive {
  color: #f15743; }
  .Button--link.is-destructive:hover {
    color: #f15743; }

.Button--headerPositive {
  border: 2px solid #FFF;
  color: #FFF; }
  .Button--headerPositive:hover {
    background-color: #FFF; }
    .Button--headerPositive:hover span {
      color: #227dbd; }

.Button-importShowDetails {
  height: 24px;
  padding: 0 11px;
  border: 1px solid #CCC;
  border-radius: 4px;
  background: transparent;
  color: #666666;
  font-size: 12px;
  line-height: 24px; }
  .Button-importShowDetails:hover {
    border-color: #666666;
    color: #666666; }

.Button--gray {
  display: inline-block;
  margin: auto;
  transition: opacity 150ms ease-in-out;
  border: 1px solid #CCC;
  border-radius: 4px;
  background-color: transparent;
  color: #2e3c43;
  font-size: 12px;
  line-height: 24px; }
  .Button--gray:hover {
    border-color: #888; }
