/* FONT FACE */
.LayoutIcon {
  display: flex;
  position: relative;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  width: 40px;
  min-width: 40px;
  height: 40px;
  border: 1px solid #cccccc;
  border-radius: 3px;
  color: #cccccc;
  font-size: 22px; }

.LayoutIcon--positive {
  border-color: #8fb83f;
  color: #8fb83f; }

.LayoutIcon--negative {
  border-color: #f15743;
  color: #f15743; }

.LayoutIcon--warning {
  border-color: #f5a623;
  color: #f5a623; }

.LayoutIcon--darkGrey {
  border-color: rgba(0, 0, 0, 0.6);
  color: #666666; }
