.OptionCards {
  display: flex;
  align-content: space-between;
  align-items: stretch; }

.OptionCard {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-content: space-between;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  border: 1px solid #dddddd;
  border-radius: 4px;
  background-color: white;
  text-align: center; }

.OptionCard--blocky {
  position: relative;
  max-width: 230px;
  padding: 32px 24px 27px; }

.OptionCard--code {
  position: relative;
  align-items: flex-start;
  max-height: 200px;
  overflow: hidden;
  background-color: #f9f9f9;
  color: #2E3C43;
  font-family: monospace;
  text-align: left; }

.OptionCard--codeRequest pre {
  white-space: pre-wrap; }

.OptionCard--codeResult {
  padding-bottom: 0; }

.OptionCard--codeResult,
.OptionCard--codeResult pre {
  overflow: hidden; }

.OptionCard--blocky .OptionCard-title {
  margin-bottom: 0; }

.OptionCard--blocky .OptionCard-desc {
  width: 100%;
  margin-top: 12px;
  margin-bottom: -12px; }

.OptionCard--onlyIcons {
  justify-content: center;
  min-height: 114px; }

.OptionCard.is-disabled {
  opacity: 0.4; }

.OptionCard.is-selected {
  border-color: #9dc2e0;
  background-color: #eff6fc;
  cursor: default; }

.OptionCard--static {
  cursor: default; }

.OptionCard:not(.is-disabled):not(.is-selected):not(.OptionCard--static):hover {
  border-color: #9dc2e0;
  background-color: #f5fafe;
  cursor: pointer; }

.OptionCard:not(:last-child) {
  margin-right: 20px; }

.OptionCard-icon {
  margin-bottom: 12px; }

.OptionCard-title {
  margin-bottom: 4px; }

.OptionCard-desc {
  width: 100%;
  margin-bottom: 12px; }

.OptionCard-shadow {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-image: linear-gradient(to bottom, rgba(249, 249, 249, 0.04) 0%, rgba(249, 249, 249, 0.95) 100%); }
