/* FONT FACE */
.UpgradeElement {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 80px;
  padding: 20px;
  border-radius: 3px;
  background: #f5fafe; }

.UpgradeElement--fixed {
  width: 500px;
  margin: 30px 0; }

.UpgradeElement--noMargins {
  padding: 0;
  border: none;
  border-radius: 0; }

.UpgradeElement-info {
  display: flex;
  align-items: center;
  justify-content: flex-start; }

.UpgradeElement-infoIcon {
  margin-right: 20px;
  border-color: #1899DD;
  color: #008EDA;
  line-height: 20px; }

.UpgradeElement-infoText {
  font-family: 'Montserrat';
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 16px;
  text-align: right; }

.UpgradeElement-actions {
  display: flex;
  align-items: center;
  justify-content: flex-start; }

.UpgradeElement-trial {
  display: flex;
  align-items: center;
  justify-content: flex-start; }

.UpgradeElement-trialIcon,
.UpgradeElement-trialText {
  color: #666666;
  font-size: 13px;
  line-height: 20px; }

.UpgradeElement-trialText {
  margin-left: 5px; }

.UpgradeElement-button {
  margin-left: 20px; }
