.CreateDialog-footer {
  position: fixed;
  z-index: 4;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0 !important;
  border: 0 !important;
  text-align: center; }

.CreateDialog-footerInner {
  display: flex;
  position: relative;
  align-items: baseline;
  justify-content: space-between;
  min-height: 72px;
  margin: 0 auto;
  padding: 16px 40px;
  background: #f9f9f9; }

@media (min-width: 760px) {
  .CreateDialog-footerInner {
    width: 760px; } }

@media (min-width: 940px) {
  .CreateDialog-footerInner {
    width: 940px; } }

.CreateDialog-footerShadow {
  position: absolute;
  z-index: 2;
  top: -19px;
  left: 50%;
  width: 1020px;
  height: 18px;
  margin-left: -510px;
  background-image: linear-gradient(to bottom, rgba(249, 249, 249, 0), #f9f9f9); }

.CreateDialog-footerLine {
  position: absolute;
  z-index: 2;
  top: -1px;
  left: 50%;
  width: 940px;
  height: 1px;
  margin-left: -470px;
  background: #dddddd; }

.CreateDialog-footerInner.is-centered {
  display: block;
  justify-content: initial; }

.CreateDialog-footerInfoIcon {
  margin-right: 8px; }

.CreateDialog-footerInfoEnquiry {
  padding-bottom: 10px; }

.CreateDialog-footerActions {
  display: flex;
  align-items: center; }

.CreateDialog-footerActionsButton.is-separated {
  margin-right: 10px; }
