.Input {
  height: 40px;
  padding: 0 12px;
  border: 1px solid #cccccc;
  border-radius: 4px;
  color: #666666; }
  .Input ::-webkit-input-placeholder {
    color: #cccccc; }
  .Input :-moz-placeholder {
    color: #cccccc; }
  .Input ::-moz-placeholder {
    color: #cccccc; }
  .Input :-ms-input-placeholder {
    color: #cccccc; }

.Input--slim {
  height: 28px;
  padding-right: 10px;
  padding-left: 10px; }

.Input.is-loading {
  background: url("/assets/1.0.0-assets.136/images/layout/loader_transparent.gif") no-repeat 10px 50%; }
