/*
  <div class="form_spinner">
    <input class="value" type="text" value="0.6" />
    <a href="#plus" class="plus"></a>
    <a href="#minus" class="minus"></a>
  </div>
*/
.form_spinner {
  display: inline-block;
  position: relative;
  width: auto;
  min-width: 20px;
  margin: 0;
  padding: 2px 19px 2px 4px;
  border: 1px solid #9E9E9E;
  border-radius: 3px;
  background: white;
  line-height: 20px; }
  .form_spinner:hover {
    cursor: pointer; }
  .form_spinner:hover,
  .form_spinner .input:hover {
    border: 1px solid #7E7E7E; }
  .form_spinner:hover input.value {
    color: #333; }
  .form_spinner input.value {
    width: 25px;
    margin: 0;
    padding: 0;
    float: none;
    border: none;
    background: none;
    color: #666;
    font-size: 13px;
    line-height: 15px;
    text-align: left; }
    .form_spinner input.value:focus {
      outline: none;
      color: #333; }
  .form_spinner a {
    position: absolute;
    right: 0;
    width: 14px;
    height: 11px;
    border-left: 1px solid #A8A8A8;
    text-indent: -9999px; }
    .form_spinner a::before {
      content: ' ';
      position: absolute;
      left: 5px;
      width: 0;
      height: 0;
      border-right: 2px solid transparent;
      border-left: 2px solid transparent; }
    .form_spinner a.plus {
      top: 0;
      border-bottom: 1px solid #A8A8A8; }
      .form_spinner a.plus::before {
        top: 4px;
        border-bottom: 3px solid #999; }
    .form_spinner a.minus {
      bottom: 0;
      height: 12px; }
      .form_spinner a.minus::before {
        top: 5px;
        border-top: 3px solid #999; }
    .form_spinner a:hover.plus::before {
      border-bottom: 3px solid #666; }
    .form_spinner a:hover.minus {
      bottom: 0; }
      .form_spinner a:hover.minus::before {
        border-top: 3px solid #666; }
  .form_spinner.disabled {
    background: #F4F4F4 !important; }
    .form_spinner.disabled a {
      opacity: 0.5;
      background: #F4F4F4 !important; }
      .form_spinner.disabled a:hover {
        cursor: default; }
    .form_spinner.disabled:hover {
      cursor: default; }
