.UISlider {
  position: relative;
  width: 140px;
  height: 10px; }

.UISlider.ui-slider {
  border-radius: 10px;
  background: white; }

.UISlider.ui-slider::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px; }

.UISlider .ui-slider-range-min {
  height: 10px;
  border-radius: 10px;
  background-color: #1181FB; }

.UISlider .ui-slider-handle {
  position: absolute;
  z-index: 3;
  top: 5px;
  left: 2px;
  width: 18px;
  height: 18px;
  margin-top: -9px;
  margin-left: -9px;
  border-radius: 18px;
  outline: none;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.15) 0 0 0 1px; }
  .UISlider .ui-slider-handle.ui-state-active, .UISlider .ui-slider-handle.ui-state-focus, .UISlider .ui-slider-handle.ui-state-hover {
    box-shadow: rgba(0, 0, 0, 0.3) 0 0 0 1px; }

.UISlider.ui-slider-disabled {
  opacity: 0.6; }

.UISlider.ui-slider-disabled .ui-slider-handle {
  cursor: default; }
